.mainpage{
    background-color: white!important;
}

.mainboard {
    background-image: url("./images/deneme1.webp");
    background-repeat: no-repeat;
    background-size: auto;
    height: 100vh !important;
    display: flex;
    flex-direction: column;
    
}
.space-md{
    height: 250px;
}
.selectmodel {
    justify-content: center;
    align-items: center;
    align-content: center;
    display: flex;
    max-height: 50px;
}
.selecmodelbtn {
    height: 40px;
    width: 170px;
    padding: 2px;
    margin: 3px;
}

.selecmodeldropdown {
    height: 40px;
    width: 270px;
    padding: 2px;
    margin: 3px;
    text-align: start;
}


.services{
    height: auto!important;
    width: 100% !important;
    margin-top: -50px!important;
   
}

.services .container {
    border-radius: 10px !important;
    
}

.services span{
color: black;
}

.services img{
    width: 50px;
    height: 50px;
    margin: 3px;
}

.box{
    background-color: #f8f9fa!important;
}

.box:first-child{
    border-top-left-radius:15px;
    border-bottom-left-radius:15px;
}

.box:last-child{
    border-top-right-radius:15px;
    border-bottom-right-radius:15px;
}

.box-shadow {
    border-bottom: solid 3px #fcdd05;
    padding-bottom: 3px;
}

.easy-service h1{
    color: black;
}

.text-hd{
    font-size: 28px !important;
    color: #37474F !important;
    font-weight: 800;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.47;
    letter-spacing: normal;
}